.Testimonial h2 {
    margin: 15px 0 0 0;
  }
  
  .Testimonial .left {
    width: 35%;
    padding: 35px;
    margin: 20px;
  }
  .Testimonial .right {
    width: 65%;
    padding: 0 0 0 50px;
    margin: 0 20px 0 0;
  }
  .Testimonial .quote i {
    font-size: 150px;
    opacity: 0.1;
  }
  
  .Testimonial .content {
    padding: 50px;
  }
  .Testimonial .content h1 {
    font-weight: 500;
  }
  .Testimonial .content h3 {
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 40px;
  }
  .Testimonial .content:hover{
    background: #ff3a75;
  }
  .Testimonial .content:hover h1{
    color: white;
  }
  .Testimonial .content:hover h3{
    color: white;
  }
  .Testimonial .content:hover p{
    color: white;
  }
  .Testimonial .slide_button {
    position: absolute;
    top: 30px;
    right: 20px;
  }
  .Testimonial .slide_button .prev_btn {
    margin-right: 30px;
    width: 8%;
    height: 8%;
    margin-left: 80%;
  }

  .Testimonial .next_btn{
    width: 8%;
    height: 8%;
  }
  
  /*-----Testimonial slide-----------*/
  .Testimonial .slide {
    max-width: 80%;
    margin: auto;
    height: 500px;
    position: relative;
    display: flex;
    overflow: hidden;
  }
  article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
  }
  article.activeSlide {
    opacity: 1;
    transform: translateX(0);
  }
  article.lastSlide {
    transform: translateX(-100%);
  }
  article.nextSlide {
    transform: translateX(100%);
  }
  /*-----Testimonial slide-----------*/
  @media (max-width: 768px) {
    article {
      flex-direction: column;
      padding: 20px;
    }
    .Testimonial .left {
      width: 100%;
      display: flex;
      margin: 0;
      margin-top: 15%;
    }
    .Testimonial .left img {
      width: 190px;
      border-radius: 10px;
      margin-right: 20px;
    }
    .Testimonial .right {
      width: 100%;
      margin: 0;
      padding: 0 0 0 0px;
    }
    .Testimonial .slide {
      height: 70vh;
      max-width: 90%;
    }
    .Testimonial .quote {
      display: none;
    }
  }
  .websiteImage{
    width:94%;
    max-height: 200px;
    margin:3%;
    margin-bottom: 0px;
    border-radius: 10%;
  }

  .websiteImage:hover{
    transform: scale(1.1);
  }

  .prev_icon{
    width: 90%;
    height: 80%;
    transform: scaleX(-1);
  }
  .next_icon{
    width: 90%;
    height: 80%;
  }

  
  
  